import React, { useState, useEffect } from 'react';
import { StyleSheet, Text, View, TouchableOpacity, Image } from 'react-native';
import { Camera } from 'expo-camera';
import { Button, Icon } from 'native-base';
import { useRoute } from '@react-navigation/native';

const ModalScreen = ({ navigation }) => {
  const [hasPermission, setHasPermission] = useState(null);
  const [type, setType] = useState(Camera.Constants.Type.back);
  const route = useRoute();
  const [photo,setPhoto] = useState();
  const [camera,setCamera] = useState();

  useEffect(() => {
    (async () => {
      const { status } = await Camera.requestPermissionsAsync();
      setHasPermission(status === 'granted');
    })();
  }, []);

  if (hasPermission === null) {
    return <View />;
  }
  if (hasPermission === false) {
    return <Text>No access to camera</Text>;
  }

  const snap = async()=>{
    if (camera) {
      let photo = await camera.takePictureAsync();
      setPhoto(photo);
    }
  };

  const goBack = () => {
    navigation.navigate(route.params.sourceScreen,{
      photo: photo,
      sourceScreen: 'CameraScreen',
      id: route.params?.id
    });
  };

  return (
    <View style={styles.container}>
      {!photo &&
      (<Camera style={styles.camera} type={type} ref={ref=>{
          setCamera(ref);
        }}>
        <View style={styles.buttonContainer}>
          <TouchableOpacity
            style={styles.button}
            onPress={() => {
              setType(
                type === Camera.Constants.Type.back
                  ? Camera.Constants.Type.front
                  : Camera.Constants.Type.back
              );
            }}>
            <View style={{ flex:1, flexDirection: 'row', justifyContent: 'space-around', alignContent: 'center', marginLeft: 20}}>
              <Text style={styles.text}> Camera sau </Text>
              <Text style={styles.text} onPress={snap}> Chụp ảnh </Text>
              <Button transparent style={{marginRight: 20}} onPress={goBack}>
                <Icon name="arrow-back" />
            </Button>
          </View>
          </TouchableOpacity>
        </View>
      </Camera>
      )}
      {photo && 
        <View style={{ flex:1, flexDirection: 'column', justifyContent: 'space-around', alignContent: 'center', marginLeft: 20}}>
          <Image source={{uri: photo.uri}} style={styles.image}>
          </Image>
          <View style={{flexDirection: "row", justifyContent: 'space-around', alignContent: 'center'}}>            
            <Button onPress={()=>{setPhoto(null)}}>
                <Text style={{fontSize: 18}}> Chụp lại </Text>
            </Button>            
            <Button onPress={goBack}>
                <Text style={{fontSize: 18}}> Tải ảnh </Text>
            </Button>
          </View>
        </View>
      }
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    alignSelf: 'center',
    width: 640,
    height: 480
  },
  image: {
    flex: 1,
    width: 640,
    height: 480
  },
  camera: {
    flex: 1
  },
  buttonContainer: {
    flex: 1,
    backgroundColor: 'transparent',
    flexDirection: 'row',
    margin: 20,
  },
  button: {
    flex: 1,
    alignSelf: 'flex-end',
    alignItems: 'center',
  },
  text: {
    fontSize: 18,
    marginLeft: 20,
    color: 'white',
  },
});

  
export default ModalScreen;