import "react-native-gesture-handler";
import { Body, Button, Container, Header, Icon, Left, Right, Tab, Tabs, Title } from "native-base";
import React, { useState } from "react";
import { SafeAreaProvider } from "react-native-safe-area-context";
import TodayTasksScreen from "components/TodayTasks";
import { ScrollView } from "react-native";
import KidReport from "@components/KidReport";

const HelloWorldApp = (props) => {

  const [tabIndex,setTab] = useState(0);
  const tabChanged = (e)=> {
    console.log(e);
    setTab(e.i);
  };

  return (
    <SafeAreaProvider>
      <Container>
        <ScrollView>
        <Header>
          <Left>
            <Button transparent>
              <Icon name="menu" />
            </Button>
          </Left>
          <Body>
            <Title>SUMO DAYS</Title>
          </Body>
          <Right>
            <Button transparent>
            <Icon name="arrow-back" />
            </Button>
          </Right>
        </Header>
        <Tabs onChangeTab={tabChanged}>
          <Tab heading="Susu">
            <TodayTasksScreen index={tabIndex} user={__DEV__? "Dad": "Susu"}/>
          </Tab>
          <Tab heading="Nemo">
          <TodayTasksScreen index={tabIndex} user="Nemo"/>
          </Tab>
          <Tab heading="Dad">
            <KidReport index={tabIndex}></KidReport>
          </Tab>
        </Tabs>
        </ScrollView>
      </Container>
    </SafeAreaProvider>
  );
};
export default HelloWorldApp;